<template>
  <div id="home">
    <div class="center-stage">
      <div class="text text-margin">{{ designConcepts.minimal }}</div>
      <div v-if="isDesktop" class="pipe-margin">|</div>
      <div class="text text-margin">{{ designConcepts.intuitive }}</div>
      <div v-if="isDesktop" class="pipe-margin">|</div>
      <div class="text text-margin">Beautiful</div>
      <!-- <i v-else class="mdi mdi-chevron-down mobile-icon" /> -->
    </div>
    <button class="button" @click="scrollTo('about')">
      <p class="button-text">Have a look</p>
      <i class="mdi mdi-chevron-down icon" />
    </button>
    <div class="svg-wrap" :class="{ 'svg-desktop': isDesktop }">
      <!-- <div id="svg-wrap"> -->
      <FlowerSVG />
    </div>
  </div>
</template>

<script>
import FlowerSVG from '@/components/FlowerSVG.vue'
import { EventBus } from '@/services/EventBus'
import Velocity from 'velocity-animate'
import 'velocity-animate/velocity.ui.js'

export default {
  props: {
    isDesktop: Boolean
  },

  components: {
    FlowerSVG
  },

  data() {
    return {
      designConcepts: {
        minimal: 'Minimal',
        intuitive: 'Intuitive',
        beautiful: 'Beautiful'
      },
      hasCalculatedSvgTopPos: false,
      svgTopPos: 100
    }
  },

  methods: {
    petalElements() {
      return document.querySelectorAll('.petal')
    },
    renderFlower() {
      Velocity(this.petalElements(), 'transition.expandIn', {
        stagger: 25,
        drag: true,
        duration: 2000
      })
      document.querySelector('#home').style.opacity = 0.97
    },
    scrollTo(section) {
      EventBus.$emit(`scroll-to`, section)
    }
  },

  mounted() {
    this.petalElements().forEach(el => {
      el.setAttribute('fill-opacity', 1)
    })

    this.renderFlower()
  }
}
</script>

