        <div class="skill" v-for="skill in skillList" :key="skill">
          <div class="check-box-wrapper">
            <CheckboxSVG ref="checkboxSVG" />
          </div>
          <div class="skill-text">{{ skill }}</div>
        </div>
      </div>
