<script>
import { EventBus } from '@/services/EventBus'
import CheckboxSVG from '@/components/CheckboxSVG.vue'
import Velocity from 'velocity-animate'
import 'velocity-ui-pack'

export default {
  props: {
    isDesktop: Boolean
  },

  components: {
    CheckboxSVG
  },

  data() {
    return {
      areConceptItemsRendered: false,
      scrollYIconRenderPoint: 362,
      skillList: [
        'JavaScript',
        'React.js',
        'Vue.js',
        'Angular',
        'Ember.js',
        'UX/UI Design',
        'SVG',
        'Web Animations',
        'CSS(SASS)',
        'HTML',
        'Node.js',
        'Express.js',
        'GraphQL',
        'SQL',
        'NoSQL'
      ]
    }
  },

  methods: {
    handleScroll() {
      if (Math.round(window.scrollY) >= this.scrollYIconRenderPoint) {
        this.renderAboutSectionElements()
      }
    },

    renderConceptElements() {
      Velocity(
        document.querySelectorAll('.concept-item-icon'),
        'transition.flipXIn',
        {
          stagger: 300,
          drag: 700,
          duration: 1500
        }
      )
      Velocity(
        document.querySelectorAll('.concept-item-text'),
        'transition.slideLeftIn',
        {
          stagger: 300,
          drag: 700,
          duration: 1500
        }
      )
      this.areConceptItemsRendered = true
    },

    renderProfileElements() {
      this.renderProfileDetailsElement()
      this.renderSkillsListElement()
    },

    renderProfileDetailsElement() {
      Velocity(
        document.getElementById('profile-details'),
        'transition.flipXIn',
        {
          stagger: 300,
          drag: 700,
          duration: 1500
        }
      )
    },

    renderSkillsListElement() {
      EventBus.$emit('render-check-box')
      Velocity(document.querySelectorAll('.check-box-wrapper'), { opacity: 1 })
      Velocity(
        document.querySelectorAll('.skill-text'),
        'transition.slideRightIn',
        {
          drag: 1000,
          duration: 1800,
          delay: 600
        }
      )
    },

    renderAboutSectionElements() {
      window.removeEventListener('scroll', this.handleScroll)
      this.renderConceptElements()
      this.renderProfileElements()
    }
  },

  created() {
    // it's good to debounce the scroll event to improve UX experience. otherwise users might experience reduced performance quality
    // from numerous scroll events being piped into the event stream
    // this.handleDebouncedScroll = debounce(this.handleScroll, 100)

    window.addEventListener('scroll', this.handleScroll)
  },

  mounted() {
    Velocity(document.querySelectorAll('.concept-item-icon'), { opacity: 0 })
    Velocity(document.querySelectorAll('.concept-item-text'), { opacity: 0 })
    Velocity(document.getElementById('profile-details'), { opacity: 0 })
    Velocity(document.querySelectorAll('.check-box-wrapper'), { opacity: 0 })
    Velocity(document.querySelectorAll('.skill-text'), { opacity: 0 })

    if (
      !this.areConceptItemsRendered &&
      window.scrollY >= this.scrollYIconRenderPoint
    ) {
      this.renderAboutSectionElements()
    }
  }
}
</script>

