<template>
  <svg
    version="1.1"
    id="layer_2"
    xmlns="http://www.w3.org/2000/svg"
    xmlns:xlink="http://www.w3.org/1999/xlink"
    width="25"
    height="25"
    viewBox="0 0 25 25"
  >
    <g class="check-box-border">
      <path
        class="left-side"
        stroke="#000000bd"
        stroke-width="4"
        stroke-dasharray="40"
        fill="none"
        d="
        M 0,25 
        v -25
        h 15"
      />
      <path
        class="right-side"
        stroke="#000000bd"
        stroke-width="4"
        stroke-dasharray="40"
        fill="none"
        d="
        M 0,25 
        h 25
        v -15"
      />
    </g>

    <g class="check-box-check">
      <path
        class="check-path"
        stroke="#000000bd"
        stroke-width="3"
        stroke-dasharray="31"
        fill="none"
        d="
        M 8,10
        l 4,8
        l 13,-18"
      />
    </g>
  </svg>
</template>

<script>
import { EventBus } from '@/services/EventBus'
import Velocity from 'velocity-animate'
import 'velocity-ui-pack'

export default {
  methods: {
    drawSVGPath(elements, startingdashoffset, endingdashoffset) {
      Velocity.animate(
        elements,
        {
          'stroke-dashoffset': startingdashoffset
        },
        0
      ).then(() => {
        Velocity.animate(
          elements,
          {
            'stroke-dashoffset': endingdashoffset
          },
          1500
        )
      })
    },

    renderCheckbox() {
      this.drawSVGPath(document.querySelectorAll('.left-side'), 40, 0)
      this.drawSVGPath(document.querySelectorAll('.right-side'), 40, 0)
      this.drawSVGPath(document.querySelectorAll('.check-path'), 31, 0)
    }
  },

  created() {
    EventBus.$on('render-check-box', () => this.renderCheckbox())
  }
}
</script>
