<template>
  <div id="app">
    <Home :is-desktop="isDesktop" />
    <Main :is-desktop="isDesktop" />
  </div>
</template>

<script>
import Home from '@/components/Home.vue'
import Main from '@/views/Main.vue'
export default {
  components: {
    Home,
    Main
  },

  data() {
    return {
      isDesktop: false
    }
  },

  methods: {
    checkScreenSize() {
      this.isDesktop = screen.width > 768
    }
  },

  mounted() {
    this.checkScreenSize()
  }
}
</script>
