<template>
  <div id="nav">
    <div class="nav-text link" @click="scrollTo('home')">Home</div>
    <!-- <div class="nav-text">|</div>
    <div class="nav-text link" @click="scrollTo('about')">About</div>
    <div class="nav-text">|</div>
    <div class="nav-text link" @click="scrollTo('design-section')"
      >Design Concepts</div
    >-->
  </div>
</template>

<script>
import { EventBus } from '@/services/EventBus'

export default {
  props: {
    isDesktop: Boolean
  },

  methods: {
    scrollTo(section) {
      EventBus.$emit('scroll-to', section)
    }
  },
  mounted() {
    EventBus.$on('scroll-to', section => {
      let el = document.getElementById(section)
      let navBarOffset = 60
      let elPos = el.offsetTop

      if (this.isDesktop) {
        let top = elPos - navBarOffset

        window.scrollTo({
          top,
          behavior: 'smooth'
        })
      } else {
        document.querySelector('html').scrollTop = elPos
      }
    })
  }
}
</script>

<style lang="scss" scoped>
#nav {
  display: flex;
  flex-direction: row;
  justify-content: center;
  background-color: #163367; //081b32 i kind of like the one in use a little better
  border-bottom: 3px solid #82bef2;
  padding: 0.1em;
  font-size: 24px;
  color: #ffffff;
  position: -webkit-sticky;
  position: sticky;
  top: 0px; // sticky prop requires a top to be defined
  z-index: 1000;
}

